@import url("https://fonts.googleapis.com/css2?family=PT+Sans&family=Work+Sans:wght@200;300;400;500&display=swap");
* {
  font-family: "Inter", sans-serif;
  line-height: 120%;
}

.text-xs {
  font-size: 14px;
}
.text-s {
  font-size: 16px;
}
.text-m {
  font-size: 20px;
}
.text-l {
  font-size: 20px;
}
.text-xl {
  font-size: 24px;
}
.text-xxl {
  font-size: 36px;
}
.text-title {
  font-size: 64px;
}

* {
  box-sizing: border-box;
}

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre,
form, fieldset, input, p, blockquote, table, th, td, embed, object {
  padding: 0;
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em,
h1, h2, h3, h4, h5, h6, strong, th, var {
  font-weight: normal;
  font-style: normal;
}

ul {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 1em;
}

q:before, q:after {
  content: "";
}

a, ins {
  text-decoration: none;
}

html,
body {
  height: auto;
}

* {
  font-size: 18px;
  color: #2D2D2A;
  scrollbar-width: thin;
  scrollbar-color: #C3C3C3 #fff;
}

a, button, input {
  outline: transparent;
}

*::-webkit-scrollbar {
  height: 12px;
  width: 9px;
}

*::-webkit-scrollbar-track {
  background: #fff;
}

*::-webkit-scrollbar-thumb {
  background-color: #C3C3C3;
  border-radius: 6px;
  border: 1px solid #fff;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  max-width: 1210px;
  margin: 0 auto;
  width: 100%;
  position: relative;
  padding: 0 20px;
}

main {
  flex: 1 0 auto;
}

section {
  margin-bottom: 80px;
}

.section-title {
  font-weight: 600;
  text-align: center;
  margin-bottom: 40px;
}

.phone {
  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
  color: #4361EE;
  padding: 12px 24px;
  display: flex;
  flex-shrink: 0;
}

.button {
  font-weight: 500;
  font-size: 18px;
  padding: 12px 24px;
  color: #fff;
  background: #4361EE;
  border: none;
  line-height: 22px;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  flex-shrink: 0;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu-item {
  text-transform: uppercase;
  position: relative;
}
.menu-item:hover > ul {
  display: flex;
}
.menu-link {
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.menu-link:hover {
  color: #4361EE;
}

.header-mobile-menu {
  display: none;
}

.header-button-wrapper {
  top: 0px;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: sticky;
  top: 1px;
  width: 375px;
  margin-left: auto;
}

.header {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #C3C3C3;
  padding: 0 20px;
  justify-content: space-between;
  max-width: 1920px;
  margin: 0 auto;
}
.header-logo {
  height: 60px;
  margin-right: auto;
  transition: all 0.4s;
}
.header-logo-wrapper {
  flex-grow: 1;
  top: 10px;
  display: flex;
  align-items: center;
  position: sticky;
  width: 375px;
  margin-right: auto;
}
.header-inner {
  max-width: 1210px;
  margin: 0 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.header-info {
  padding: 14px 0 18px;
  border-bottom: 1px solid #C3C3C3;
  display: flex;
  align-items: center;
}
.header-info__search {
  margin-right: auto;
  border: 1px solid #C3C3C3;
  border-radius: 4px;
  display: flex;
  align-items: center;
  padding: 11px;
  cursor: pointer;
  max-width: 240px;
  transition: all 0.3s;
}
.header-info__search-input {
  transition: all 0.3s;
  border: none;
  line-height: 22px;
  display: flex;
  width: 0;
  padding: 0;
}
.header-info__phone {
  margin-right: auto;
  padding-left: 16px;
}
.header-info__phone:hover {
  color: #F72585;
}
.header-info__work {
  margin-right: auto;
  padding-left: 16px;
}
.header-info__work-title {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 4px;
}
.header-info__work-hours {
  font-size: 18px;
}
.header-info__work-hours sup {
  font-size: 14px;
}
.header-info__work-hours span {
  font-weight: 500;
  font-weight: 500;
}
.header-info__contacts {
  padding-left: 16px;
}
.header-info__contacts-address {
  margin-bottom: 4px;
}
.header-info__contacts-email:hover {
  color: #4361EE;
}
.header-menu {
  top: 0;
}
.header-menu__item {
  display: flex;
}
.header-menu__link {
  padding: 16px 32px;
}
.header-button {
  margin-left: auto;
}
.header-button:hover {
  background: #F72585;
}

.sticky {
  position: sticky;
  top: -86px;
  z-index: 5;
  background: #fff;
}

.hero-wrapper {
