  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}
.advantages-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.advantages-item__image {
  object-fit: cover;
  width: 100%;
  max-width: 64px;
}
.advantages-item__title {
  padding: 24px 0 8px;
  font-weight: 600;
  text-align: center;
}
.advantages-item__description {
  text-align: center;
  width: 100%;
  font-weight: 400;
