}

.hidden {
  display: none;
}

.about-inner {
  display: flex;
  align-items: center;
  gap: 0 100px;
}
.about-image {
  object-fit: cover;
  width: 100%;
  max-width: 500px;
}
.about-text {
  display: flex;
  flex-direction: column;
  max-width: 570px;
  width: 100%;
}
.about-text__description {
  line-height: 140%;
  margin-bottom: 32px;
}
.about-text__button {
  display: flex;
  align-items: center;
  border: none;
  background: transparent;
  cursor: pointer;
}
.about-text__button:hover {
  color: #4361EE;
}
.about-text__button-icon {
  width: 24px;
  height: 24px;
}

.services-inner {
  display: flex;
}
.services-tabs {
  display: flex;
  flex-direction: column;
  flex: 1 0;
  min-width: 380px;
}
.services-tab {
  padding: 14px 24px;
  position: relative;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  flex-shrink: 0;
  border-radius: 4px;
}
.services-tab.active {
  background: right/cover no-repeat url("../assets/tab.svg");
  color: #fff;
  font-weight: 600;
  justify-content: center;
}
.services-tab:hover {
  color: #fff;
  background: right/cover no-repeat url("../assets/tab.svg");
}
.services-texts {
  width: 100%;
  height: 100%;
}
.services-text {
  display: none;
  align-items: center;
  gap: 0 30px;
}
.services-text__image {
  object-fit: cover;
  width: 100%;
  max-width: 300px;
}
.services-text__description {
  width: 100%;
  line-height: 140%;
  max-width: 370px;
  display: flex;
  flex-shrink: 1;
}

.prices-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.prices-title {
  margin-bottom: 4px;
}
.prices-subtitle {
  text-align: center;
  margin-bottom: 40px;
}
.prices-cards {
  display: flex;
  width: 100%;
  gap: 30px;
  justify-content: center;
  flex-wrap: wrap;
}
.prices-card {
  display: flex;
  max-width: 270px;
  flex-direction: column;
  border: 1px solid #C3C3C3;
  border-radius: 4px;
  padding: 16px 19px;
  cursor: pointer;
}
.prices-card:hover .prices-card__icon {
  position: relative;
  transform: translateX(38px);
  filter: brightness(1);
}
.prices-card__image {
  margin: 0 10px 8px;
  object-fit: cover;
  width: 100%;
  max-width: 200px;
}
.prices-card__title {
  text-align: center;
  font-size: 16px;
  line-height: 120%;
  margin-bottom: 27px;
}
.prices-card__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.prices-card__price {
  color: #4361EE;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
}
.prices-card__price span {
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  color: inherit;
  margin-left: 4px;
}
.prices-card__icon {
  width: 40px;
  height: 40px;
  filter: brightness(0.1);
  transition: all 0.3s;
}
.prices-button {
  background: transparent;
  color: #2D2D2A;
  margin-top: 40px;
  border: 1px solid #4361EE;
}
.prices-button:hover {
  border: 1px solid #F72585;
}

.feedback {
  background: #4361EE;
}
.feedback:last-child {
  margin-bottom: 0;
}
.feedback-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.feedback-wrapper::after {
  position: absolute;
  top: -2px;
  left: 390px;
  content: "";
  width: 250px;
  height: 101%;
  background: no-repeat url("../assets/separator.svg");
  background-size: auto 100%;
}
.feedback-right .feedback-wrapper::after {
  transform: rotate(180deg);
  position: absolute;
  top: -3px;
  left: 540px;
  content: "";
  width: 250px;
  height: 101%;
  background: no-repeat url("../assets/separator.svg");
  background-size: auto 100%;
}
.feedback-right .feedback-form {
  order: 1;
}
.feedback-right .feedback-image {
  order: 2;
}
.feedback-image {
  margin: 0 10px 8px;
  object-fit: cover;
  width: 100%;
  max-width: 300px;
}
.feedback-form {
  display: flex;
  flex-direction: column;
  max-width: 470px;
  align-items: flex-start;
  margin: 40px 0;
}
.feedback-form__title {
  font-weight: 600;
  font-size: 36px;
  line-height: 44px;
  color: #fff;
  margin-bottom: 8px;
}
.feedback-form__subtitle {
  font-weight: 400;
  color: #fff;
  margin-bottom: 30px;
}
.feedback-form__input {
  width: 370px;
  background: #fff;
  padding: 14px 16px;
  font-size: 16px;
  line-height: 120%;
  border: none;
  border-radius: 4px;
  margin-bottom: 16px;
}
.feedback-form__input::placeholder {
  color: #2D2D2A;
}
.feedback-form__button {
  margin-top: 8px;
  background: #F72585;
}
.feedback-form__button:hover {
  background: #4361EE;
  padding: 11px 23px;
  border: 1px solid #F72585;
}

.how .advantages-item__image {
  margin-bottom: 24px;
}

.team {
  position: relative;
}

.slider-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.swiper-button-next, .swiper-button-prev {
  background: url("../assets/slider-arrow.svg") center/cover no-repeat;
  width: 40px;
  height: 40px;
  position: absolute;
  content: "";
  top: 50%;
  transform: translate(0, -50%);
  margin-top: 40px;
  cursor: pointer;
}

.swiper-button-prev {
  left: -70px;
  transform: rotate(180deg) translateY(50%);
}

.swiper-button-next {
  right: -70px;
}

.swiper {
  display: flex;
  overflow: hidden;
  position: relative;
}
.team-slider__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 4px;
  border: 1px solid #C3C3C3;
  padding: 16px 35px;
}
.team-slider__item-image {
  object-fit: cover;
  width: 100%;
  max-width: 300px;
}
.team-slider__item-position {
  line-height: 140%;
  margin-bottom: 16px;
  margin-top: 8px;
}
.team-slider__item-lastname {
  font-size: 24px;
  text-transform: capitalize;
  line-height: 29px;
  margin-bottom: 4px;
  color: #4361EE;
  font-weight: 600;
}
.team-slider__item-name {
  margin-bottom: 10px;
  color: #4361EE;
  font-weight: 600;
}

.reviews-slider__item {
  padding: 24px 32px;
  display: flex;
  align-items: center;
  gap: 0 30px;
  max-width: 570px;
  width: 100%;
  border-radius: 4px;
  border: 1px solid #C3C3C3;
}
.reviews-slider__item-image {
  object-fit: cover;
  width: 100%;
  max-width: 240px;
}
.reviews-slider__item-inner {
  display: flex;
  flex-direction: column;
}
.reviews-slider__item-name {
  margin-bottom: 16px;
  color: #4361EE;
}
.reviews-slider__item-description {
  line-height: 140%;
  margin-bottom: 16px;
}
.reviews-slider__item-date {
  font-size: 14px;
  line-height: 140%;
}

.footer {
  background: #2D2D2A;
}
.footer-wrapper {
  margin: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer-logo {
  object-fit: cover;
  width: 100%;
  max-width: 180px;
  margin-bottom: 32px;
}
.footer-menu {
  gap: 40px;
  margin-bottom: 24px;
}
.footer-menu__link {
  cursor: pointer;
  text-transform: capitalize;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}
.footer-email {
  color: #fff;
  padding: 12px 24px;
  font-size: 18px;
  line-height: 22px;
  color: #4361EE;
}
.footer-email:hover {
  color: #F72585;
}
.footer-phone:hover {
  color: #F72585;
}
.footer-address, .footer-work {
  margin-top: 16px;
  color: #fff;
  font-size: 14px;
  line-height: 17px;
}
.footer-address sup, .footer-work sup {
  color: #fff;
  font-size: 11px;
}

.calculator-title {
  margin-bottom: 4px;
}
.calculator-subtitle {
  text-align: center;
  margin-bottom: 40px;
}
.calculator-inner {
  display: flex;
  flex-direction: column;
  border: 1px solid #C3C3C3;
  border-radius: 4px;
  padding: 40px 35px;
}
.calculator-inner-title {
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  margin: 16px 0;
}
.calculator-block {
  display: flex;
  align-items: center;
}

.total-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 80px;
}
.total-title {
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
}
.total-block {
  display: flex;
  flex-direction: column;
}
.total-discount {
  font-weight: 600;
  font-size: 20px;
  color: #F72585;
  margin-top: 13px;
}
.total-price {
  font-size: 24px;
  color: #4361EE;
  font-weight: 600;
  margin-top: 16px;
}
.total-price span {
  color: #4361EE;
  font-weight: 400;
  font-size: 14px;
}
.total-button {
  margin-top: auto;
}
.total-button:hover {
  background: #F72585;
}

.options-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  flex-direction: column;
  max-height: 170px;
  align-items: flex-end;
}
.options-block {
  display: flex;
  max-width: 33%;
  justify-content: flex-end;
}
.options-block .output-block {
  margin-left: 16px;
}
.options-title {
  max-width: 210px;
  text-align: right;
  font-size: 14px;
  line-height: 17px;
}

.range-wrapper {
  position: relative;
}

.range-slider {
  width: 0%;
  height: 8px;
  background: #4361EE;
  position: absolute;
  bottom: 8px;
  left: 0;
  border-radius: 4px;
}

input[type=range] {
  appearance: none;
  width: 100%;
  height: 24px;
  background: transparent;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 8px;
  cursor: pointer;
  background: transparent;
  border-radius: 4px;
  appearance: none;
  position: relative;
  z-index: 1;
  background-size: 50% 100%;
  background-repeat: no-repeat;
}

input[type=range]::-webkit-slider-thumb {
  height: 24px;
  position: relative;
  z-index: 1;
  width: 24px;
  border-radius: 4px;
  margin-top: -8px;
  background: #4361EE;
  cursor: pointer;
  border: none;
  appearance: none;
}

input[type=range]::-moz-range-track {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 8px;
  cursor: pointer;
  background: transparent;
  border-radius: 4px;
  appearance: none;
  background-image: linear-gradient(#4361EE, #4361EE);
  background-size: 50% 100%;
  background-repeat: no-repeat;
}

input[type=range]::-moz-range-thumb {
  height: 24px;
  position: relative;
  z-index: 1;
  width: 24px;
  border-radius: 4px;
  background: #4361EE;
  cursor: pointer;
  border: none;
  appearance: none;
}

.range {
  position: relative;
}
.range-block {
  margin-bottom: 24px;
}
.range::after {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  left: 0;
  height: 6px;
  border: 1px solid #C3C3C3;
  border-radius: 4px;
  z-index: -1;
}
.range-label {
  display: flex;
  font-size: 16px;
  line-height: 19px;
  min-width: 250px;
}
.range-wrapper {
  display: flex;
  margin-left: 30px;
  flex-grow: 1;
  flex-direction: column;
}
.range-values {
  padding: 0 5px;
  display: flex;
  justify-content: space-between;
  line-height: 17px;
}
.range-values li {
  font-size: 14px;
}

.output-block {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-left: 30px;
}
.output-block div {
  border: 1px solid #C3C3C3;
  border-radius: 4px;
  width: 40px;
  height: 40px;
}
.output-minus, .output-plus {
  position: relative;
  cursor: pointer;
}
.output-minus::before, .output-plus::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 2px;
  background: #2D2D2A;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.output-plus::after {
  position: absolute;
  content: "";
  width: 14px;
  height: 2px;
  background: #2D2D2A;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(90deg);
}
.output-number {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: 16px;
  border: 1px solid #C3C3C3;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  appearance: none;
  text-align: center;
  padding: 3px;
}

input[type=checkbox] {
  display: none;
}
input[type=checkbox]:checked + label {
  background-color: #4361EE;
}
input[type=checkbox]:checked + label:before {
  transform: translateX(12px);
  background-color: #FFF;
}

.toggle-wrapper {
  display: flex;
  align-items: center;
}
.toggle-block {
  margin-top: -4px;
  margin-bottom: 20px;
}
.toggle-label {
  display: flex;
  width: 36px;
  height: 24px;
  border-radius: 20px;
  position: relative;
  cursor: pointer;
  background: #c3c3c3;
}
.toggle-label:before {
  transition: transform 0.25s ease;
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
  top: 2px;
  left: 2px;
}
.toggle-text {
  font-size: 16px;
  line-height: 19px;
  margin-left: 4px;
  margin-right: 25px;
}

.total-radio input[type=radio] {
  display: none;
}

.total-radio label {
  display: inline-block;
  cursor: pointer;
